import React, { useState } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Col, Row, Table } from 'react-bootstrap'
import Sidebar from './SideBar'
import { getListOfNotes, getCircleOfFifths, getKeysByRoot } from './logic'

function App() {
  const [note, setNote] = useState('')
  const [major, setMajor] = 
    useState(
      { notes: [], 
        chords: {
          one:{type:'',notes:['','','']},
          two:{type:'',notes:['','','']},
          three:{type:'',notes:['','','']},
          four:{type:'',notes:['','','']},
          five:{type:'',notes:['','','']},
          six:{type:'',notes:['','','']},
          seven:{type:'',notes:['','','']}
        } })
  const [minor, setMinor] = 
    useState(
      { notes: [], 
        chords: {
          one:{type:'',notes:['','','']},
          two:{type:'',notes:['','','']},
          three:{type:'',notes:['','','']},
          four:{type:'',notes:['','','']},
          five:{type:'',notes:['','','']},
          six:{type:'',notes:['','','']},
          seven:{type:'',notes:['','','']}
        } })
  const [showSharps, setShowSharps] = useState(false)

  const HandleClick = (note) => {
    setNote(note)
    setMajor(getKeysByRoot(note, showSharps).major)
    setMinor(getKeysByRoot(note, showSharps).minor)
  }

  const HandleChange = () => {
    setShowSharps(!showSharps)
    setMajor(getKeysByRoot(note, !showSharps).major)
    setMinor(getKeysByRoot(note, !showSharps).minor)

  }

  const CreateTable = () => {
    return (
      <div >
        <Table striped bordered hover variant="dark">
          <thead>
            <tr>
              <th>Scale</th>
              <th>1</th><th>2</th><th>3</th><th>4</th><th>5</th><th>6</th><th>7</th>
            </tr>
          </thead>
          <tbody>
            <tr><td>{note} Major</td>{major.notes.map((degree, index) => { return (<td key={index}>{degree}</td>) })}</tr>
            <tr><td>{note} Minor</td>{minor.notes.map((degree, index) => { return (<td key={index}>{degree}</td>) })}</tr>
          </tbody>
        </Table>
      </div>
    )
  }

  const ShowChords = () => {
    return (
      <div >
        <Table striped bordered hover variant="dark">
          <thead>
            <tr>
              <th>{note} Major Scale Chords</th>
              <th>Type</th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{major.notes[0]}</td><td>{major.chords.one.type}</td>
              <td>{major.chords.one.notes[0]}</td><td>{major.chords.one.notes[1]}</td><td>{major.chords.one.notes[2]}</td>
            </tr>
            <tr>
              <td>{major.notes[1]}</td><td>{major.chords.two.type}</td>
              <td>{major.chords.two.notes[0]}</td><td>{major.chords.two.notes[1]}</td><td>{major.chords.two.notes[2]}</td>
            </tr>
            <tr>
              <td>{major.notes[2]}</td><td>{major.chords.three.type}</td>
              <td>{major.chords.three.notes[0]}</td><td>{major.chords.three.notes[1]}</td><td>{major.chords.three.notes[2]}</td>
            </tr>
            <tr>
              <td>{major.notes[3]}</td><td>{major.chords.four.type}</td>
              <td>{major.chords.four.notes[0]}</td><td>{major.chords.four.notes[1]}</td><td>{major.chords.four.notes[2]}</td>
            </tr>
            <tr>
              <td>{major.notes[4]}</td><td>{major.chords.five.type}</td>
              <td>{major.chords.five.notes[0]}</td><td>{major.chords.five.notes[1]}</td><td>{major.chords.five.notes[2]}</td>
            </tr>
            <tr>
              <td>{major.notes[5]}</td><td>{major.chords.six.type}</td>
              <td>{major.chords.six.notes[0]}</td><td>{major.chords.six.notes[1]}</td><td>{major.chords.six.notes[2]}</td>
            </tr>
            <tr>
              <td>{major.notes[6]}</td><td>{major.chords.seven.type}</td>
              <td>{major.chords.seven.notes[0]}</td><td>{major.chords.seven.notes[1]}</td><td>{major.chords.seven.notes[2]}</td>
            </tr>
          </tbody>
        </Table>
        <Table striped bordered hover variant="dark">
          <thead>
            <tr>
              <th>{note} Minor Scale Chords</th>
              <th>Type</th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{minor.notes[0]}</td><td>{minor.chords.one.type}</td>
              <td>{minor.chords.one.notes[0]}</td><td>{minor.chords.one.notes[1]}</td><td>{minor.chords.one.notes[2]}</td>
            </tr>
            <tr>
              <td>{minor.notes[1]}</td><td>{minor.chords.two.type}</td>
              <td>{minor.chords.two.notes[0]}</td><td>{minor.chords.two.notes[1]}</td><td>{minor.chords.two.notes[2]}</td>
            </tr>
            <tr>
              <td>{minor.notes[2]}</td><td>{minor.chords.three.type}</td>
              <td>{minor.chords.three.notes[0]}</td><td>{minor.chords.three.notes[1]}</td><td>{minor.chords.three.notes[2]}</td>
            </tr>
            <tr>
              <td>{minor.notes[3]}</td><td>{minor.chords.four.type}</td>
              <td>{minor.chords.four.notes[0]}</td><td>{minor.chords.four.notes[1]}</td><td>{minor.chords.four.notes[2]}</td>
            </tr>
            <tr>
              <td>{minor.notes[4]}</td><td>{minor.chords.five.type}</td>
              <td>{minor.chords.five.notes[0]}</td><td>{minor.chords.five.notes[1]}</td><td>{minor.chords.five.notes[2]}</td>
            </tr>
            <tr>
              <td>{minor.notes[5]}</td><td>{minor.chords.six.type}</td>
              <td>{minor.chords.six.notes[0]}</td><td>{minor.chords.six.notes[1]}</td><td>{minor.chords.six.notes[2]}</td>
            </tr>
            <tr>
              <td>{minor.notes[6]}</td><td>{minor.chords.seven.type}</td>
              <td>{minor.chords.seven.notes[0]}</td><td>{minor.chords.seven.notes[1]}</td><td>{minor.chords.seven.notes[2]}</td>
            </tr>
          </tbody>
        </Table>
      </div>
    )
  }

  return (
    <Container fluid='true'>
      <Row>
        <Col ><Sidebar handleClick={HandleClick} handleChangeShowSharps={HandleChange} showSharps={showSharps} /></Col>
      </Row>
      <Row>
        <Col className='mt-6 bg-secondary' ><CreateTable onHandleChange={HandleChange} /></Col>
      </Row>
      <Row>
        <Col className='mt-6 bg-secondary'><ShowChords/></Col>
      </Row>
    </Container >
  );
}

export default App;
