const getListOfNotes = (showSharps=true) => {
    if(showSharps){
        return (['A', 'A#', 'B', 'C', 'C#', 'D', 'D#', 'E', 'F', 'F#', 'G', 'G#'])
    }else{
       return (['A', 'Bb', 'B', 'C', 'Db', 'D', 'Eb', 'E', 'F', 'Gb', 'G', 'Ab'])
    }
}


const getChordsInKey = (key = [],isMajor=true) => {
    if (key.length === 0) { return ({}) }
    const createChord = (type = '', notes = []) => { return { 'type': type, 'notes': notes } }
    const one = createChord(isMajor?'major':'minor', [key[0], key[2], key[4]])
    const two = createChord(isMajor?'minor':'diminished', [key[1], key[3], key[5]])
    const three = createChord(isMajor?'minor':'major', [key[2], key[4], key[6]])
    const four = createChord(isMajor?'major':'minor', [key[3], key[5], key[0]])
    const five = createChord(isMajor?'major':'minor', [key[4], key[6], key[1]])
    const six = createChord(isMajor?'minor':'major', [key[5], key[0], key[2]])
    const seven = createChord(isMajor?'diminished':'major', [key[6], key[1], key[3]])
    return ({ 'one': one, 'two': two, 'three': three, 'four': four, 'five': five, 'six': six, 'seven': seven })
}
const getCorrectAccent = (note='',showSharps=true) => {
    if(note==='A')return 'A'
    if(note==='A#' || note==='Bb') return (showSharps?'A#':'Bb')
    if(note==='B') return ('B')
    if(note==='C') return ('C')
    if(note==='C#' || note==='Db') return (showSharps?'C#':'Db')
    if(note==='D') return ('D')
    if(note==='D#' || note==='Eb') return (showSharps?'D#':'Eb')
    if(note==='E') return ('E')
    if(note==='F') return ('F')
    if(note==='F#' || note==='Gb') return (showSharps?'F#':'Gb')
    if(note==='G') return ('G')
    if(note==='G#' || note==='Ab') return (showSharps?'G#':'Ab')
}

const getCircleOfFifths = (showSharps=true) => {
    const notes = getListOfNotes(showSharps);
    const circleOfFifths = []
    for (let index = 0; index < 12; index++) {
        circleOfFifths.push(notes[(index + 7) % 12])
    }
    return (circleOfFifths)
}

const getKeysByRoot = (root = '', showSharps = true) => {
    const note = getCorrectAccent(root,showSharps)
    const notes = getListOfNotes(showSharps)
    let rootIndex = notes.indexOf(note)
    const major = []
    const minor = []
    major.push(notes[rootIndex])
    major.push(notes[(rootIndex + 2) % 12])
    major.push(notes[(rootIndex + 4) % 12])
    major.push(notes[(rootIndex + 5) % 12])
    major.push(notes[(rootIndex + 7) % 12])
    major.push(notes[(rootIndex + 9) % 12])
    major.push(notes[(rootIndex + 11) % 12])
    minor.push(notes[rootIndex])
    minor.push(notes[(rootIndex + 2) % 12])
    minor.push(notes[(rootIndex + 3) % 12])
    minor.push(notes[(rootIndex + 5) % 12])
    minor.push(notes[(rootIndex + 7) % 12])
    minor.push(notes[(rootIndex + 8) % 12])
    minor.push(notes[(rootIndex + 10) % 12])

    return {
        'major': {
            'notes': major,
            'chords': getChordsInKey(major,true)
        },
        'minor': {
            'notes': minor,
            'chords': getChordsInKey(minor,false)
        }
    }

}
export { getListOfNotes, getCircleOfFifths, getKeysByRoot }