import React from "react";
import { Navbar,Nav } from "react-bootstrap";
import { getListOfNotes } from "./logic";

const Sidebar = ({ showSharps=false,handleChangeShowSharps=()=>alert('not implemented'),handleClick = (i) => alert(i) }) => {
    const notes = getListOfNotes(showSharps);
    return (
        <Navbar expand='large' className='bg-dark mb-2'>
            <Nav.Item onClick={handleChangeShowSharps} >
                <a href="#" className='nav-link text-light'>{showSharps?"Sharps":"Flats"}</a>
                </Nav.Item>
                {notes.map((note, index) => {
                    return (
                        <Nav.Item onClick={() => handleClick(note)} key={index}>
                            <a href='#' className='nav-link text-light'>{note}</a>
                        </Nav.Item>)
                })}
        </Navbar>
    );
};
export default Sidebar